@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}



.container{width:580px;height:100px;background:#F7F2EF;margin-left:auto;margin-right:auto;margin-top:2%;margin-bottom:0;padding:0;display:grid;grid-template-columns:repeat(5, 100px);grid-column-gap:20px}.containericons{width:260px;height:50px;background:#F7F2EF;margin-left:auto;margin-right:auto;margin-top:1%;margin-bottom:0;padding:0;display:grid;grid-template-columns:repeat(5, 50px);grid-column-gap:10px}.container .box{width:100%;height:100%;background:#ccc;display:grid;box-sizing:border-box;border:1px solid #354654;-webkit-transition:.5s;transition:.5s;opacity:.5}.container .box:hover{background:#851111;-webkit-transform:scale(1.1);transform:scale(1.1);border:2px solid #0A100D;border-radius:10%;opacity:.8}.container .box:active{-webkit-transform:scale(0.8);transform:scale(0.8)}.container .box h1{margin:0;padding:0;justify-self:center;align-self:center;font-size:18px;-webkit-transition:.5s;transition:.5s;color:#0A100D}.container .box:hover h1{font-size:20px;color:#ccc;cursor:pointer}a{color:#0A100D;text-decoration:none}a:visited:hover{color:#ccc}a:visited{color:#0A100D}.hero-link{width:100%;height:100%}.hero{width:300px;height:200px;background:#F7F2EF;margin-top:3%;padding:0;margin-left:auto;margin-right:auto;border-radius:50%;-webkit-transition:.5s;transition:.5s;background-size:cover;opacity:1}.hero:hover{opacity:.7;cursor:pointer}.hero:active{opacity:1}.hero-name{color:#0A100D;margin-top:20px;margin-left:auto;margin-right:auto;margin-bottom:0;padding:0;font-size:45px;font-weight:bolder;opacity:.8}.hero-profession{color:#0A100D;margin-top:20px;margin-left:auto;margin-right:auto;margin-bottom:0;padding:0;font-size:25px;opacity:.7}.hero-resume{color:#0A100D;margin-top:20px;margin-left:auto;margin-right:auto;margin-bottom:10px;padding:0;font-size:35px;opacity:.8;display:inline-block}.hero-resume:after{content:'';background-color:#354654;left:0;bottom:0;height:1.5px;width:0;-webkit-transition:width .3s ease;transition:width .3s ease;-webkit-transform:translate(-50%);transform:translate(-50%);display:block;z-index:-1;margin-left:50%}.hero-resume:hover::after{width:100%}.hero-resume:hover{cursor:pointer}.hero-resume:active::after{width:0;background-color:#851111}.icon{color:#0A100D;display:grid;box-sizing:border-box;-webkit-transition:.5s;transition:.5s;opacity:.5}.icon:hover{color:#354654;-webkit-transform:scale(1.2);transform:scale(1.2);opacity:1;cursor:pointer}.icon:active{-webkit-transform:scale(0.8);transform:scale(0.8)}hr{opacity:.5;width:600px;height:0.01px;color:#354654;margin-top:25px;-webkit-transition:.2s;transition:.2s}hr:hover{opacity:1;-webkit-transform:scale(1.01);transform:scale(1.01);color:#0A100D}.container-icon{width:30px;height:30px}.quote{font-size:16px;font-style:italic;margin-top:2%;margin-left:50px;margin-right:50px}@media screen and (max-width: 647px){.container{width:330px;height:200px;grid-template-columns:repeat(3, 100px);grid-column-gap:20px;grid-row-gap:30px}.containericons{width:260px}hr{width:350px}.container .box h1{font-size:16px}.container .box:hover h1{font-size:16px}.container .box:hover{background:#851111;-webkit-transform:scale(1);transform:scale(1);border:1px solid #0A100D}.container .boxd{grid-row:1/3;border:0px;background:#F7F2EF;grid-column:3}}@media screen and (max-width: 367px){.container{width:75px;height:250px;grid-auto-flow:row;grid-template-columns:repeat(1, 70px);grid-row-gap:10px;margin-bottom:25px}.containericons{width:75px;height:50px;grid-auto-flow:row;grid-template-columns:repeat(1, 70px);grid-column-gap:20px;grid-row-gap:3px;margin:0}hr{width:150px}.container .box h1{font-size:14px}.container .box:hover h1{font-size:14px}.container .box:hover{background:#851111;-webkit-transform:scale(1);transform:scale(1);border:1px solid #0A100D}.container .boxd{display:none}h5{margin-top:0;margin-left:30px}.hero-name{font-size:40px}.hero{width:150px;height:150px}}

.nav-bar{display:grid;grid-template-columns:repeat(5, 1fr);grid-auto-columns:minmax(120px, 120px);grid-gap:10px;max-width:50%;margin-left:auto;margin-right:auto;margin-bottom:3%;margin-top:10px}.link-nav{font-size:20px;width:100%;border:1px solid #354654;background:#ccc;height:30px;-webkit-transition:.5s;transition:.5s;opacity:.7}.link-nav:hover{background:#851111;-webkit-transform:scale(1.05);transform:scale(1.05);border:1px solid #0A100D;opacity:.9;border-top-left-radius:5px;border-top-right-radius:5px;border-bottom-right-radius:5px;border-bottom-left-radius:5px}.active{background:#851111;opacity:.9;border-top-left-radius:5px;border-top-right-radius:5px;border-bottom-right-radius:5px;border-bottom-left-radius:5px}.inactive{background:#ccc}.text-nav{margin-top:3px}@media screen and (max-width: 1370px){.nav-bar{max-width:80%}}@media screen and (max-width: 367px){.nav-bar{max-width:95%}}

.hero-description{width:50%;margin-left:auto;margin-right:auto;margin-top:1%;font-size:30px;font-weight:bold}.hero-img{width:400px;height:400px;background:#F7F2EF;margin-top:3%;padding:0;margin-left:auto;margin-right:auto;background-size:cover;opacity:0.95;-webkit-transition:.5s;transition:.5s;-webkit-transform:translateX(-20%);transform:translateX(-20%)}.hero-img:hover{opacity:1}@media screen and (max-width: 430px){.hero-img{width:300px;height:300px}}@media screen and (max-width: 250px){.hero-img{width:200px;height:200px}}

.box-posts{margin-top:5%;margin-left:auto;margin-right:auto;background:#F7F2EF;width:400px}.box-posts .post{width:100%;height:75px;background:#ccc;box-shadow:0 5px 25px rgba(0,0,0,0.1);-webkit-transition:0.1s;transition:0.1s;margin-top:10%;font-size:15px;font-weight:600;opacity:0.7;-webkit-transition:0.3s;transition:0.3s;text-align:left}.blog-img{padding-left:15%;width:50px;height:50px;border-radius:50%}.box-posts .post:hover{-webkit-transform:scale(1.1);transform:scale(1.1);z-index:100;background:#354654;box-shadow:0 5px 25px rgba(0,0,0,0.2);color:#F7F2EF;opacity:1;border-top-left-radius:20px;border-top-right-radius:20px;border-bottom-right-radius:20px;border-bottom-left-radius:20px}.box-posts .post:active{-webkit-transform:scale(1);transform:scale(1)}.box-posts .post-id{width:20px;height:20px;text-align:center;line-height:20px;background:#354654;color:#F7F2EF;display:inline-block;border-radius:50%;margin-right:10px;font-size:12px;font-weight:600;-webkit-transform:translateY(-5px);transform:translateY(-5px);-webkit-transition:0.5s;transition:0.5s;margin-top:7%}.date-posted{position:absolute;-webkit-transform:translate(350%, 100%);transform:translate(350%, 100%)}.likes-blog{position:absolute;-webkit-transform:translate(25px, 100%);transform:translate(25px, 100%)}.box-posts .post:hover .post-id{background:#F7F2EF;color:#354654}@media screen and (max-width: 430px){.box-posts{width:200px}.date-posted{-webkit-transform:translate(130%, 100%);transform:translate(130%, 100%)}.box-posts .post{margin-top:20%}.blog-img{padding-left:5%}.likes-blog{-webkit-transform:translate(5px, 100%);transform:translate(5px, 100%)}}

.content{display:grid;grid-template-columns:repeat(6, 1fr);grid-auto-rows:minmax(60px, auto);grid-gap:10px;max-width:50%;margin-left:auto;margin-right:auto;margin-top:4%}.content a{padding:25px;border:1px solid #354654;color:#F7F2EF;font-size:18px;background-repeat:no-repeat;background-size:cover;border-top-left-radius:5px;border-top-right-radius:5px;border-bottom-right-radius:5px;border-bottom-left-radius:5px}.content a:hover{background-color:#354654;border:1px solid #0A100D;-webkit-transform:scale(1.3);transform:scale(1.3);-webkit-transition:0.5s;transition:0.5s;cursor:pointer}.content a:active{-webkit-transform:scale(1);transform:scale(1)}.text-item{display:flex;text-align:left}.porfolio-item-one{background-image:url("https://raw.githubusercontent.com/raulgavris/website-portfolio/master/frontend/assets/website-portfolio.png");grid-column:1/3;grid-row:1/3}.porfolio-item-two{background-image:url("https://raw.githubusercontent.com/raulgavris/website-portfolio/master/frontend/assets/dotfiles-vim-zsh.png");grid-column:3/5;grid-row:1/3}.porfolio-item-three{background-image:url("https://raw.githubusercontent.com/raulgavris/website-portfolio/master/frontend/assets/arch-linux.png");background-position-y:50%;grid-column:1/5;grid-row:3/4}.porfolio-item-four{grid-column:5/7;grid-row:1/4}.porfolio-item-five{grid-column:1/7;grid-row:4/5}.porfolio-item-six{grid-column:1/3;grid-row:5/7}.porfolio-item-seven{grid-column:3/7;grid-row:5/7}.porfolio-item-eight{grid-column:1/5;grid-row:7/9}.porfolio-item-nine{grid-column:5/7;grid-row:7/9}.porfolio-item-ten{grid-column:1/7;grid-row:9/10}@media screen and (max-width: 647px){.content{max-width:80%}}@media screen and (max-width: 367px){.content{max-width:95%}}

.success{font-size:15px;text-align:center;color:#0A100D;opacity:1;background-color:#F7F2EF;width:100%;font-family:monospace;font-weight:bold;font-weight:bold;border:3px solid #0A100D;-webkit-transition:.5s;transition:.5s}.success:hover{color:#354654}.failed{font-size:15px;text-align:center;color:#F7F2EF;opacity:0.7;background-color:#851111;width:100%;font-family:monospace;font-weight:bold;border:3px solid #0A100D;-webkit-transition:.5s;transition:.5s}.failed:hover{opacity:1}

Form{width:500px;margin-left:auto;margin-right:auto;margin-top:5%}.formgroup{width:500px;background:transparent;border:none;outline:none;font-size:25px;margin-bottom:20px}.label{margin-right:10px;display:flex;flex-direction:row;width:100%;justify-content:center;margin-bottom:3%}.input{width:300px;height:25px;background:transparent;border:none;outline:none;border-bottom:2px solid #0A100D;resize:none;font-size:20px;font-family:'VT323', sans-serif;color:#354654}.textarea{height:100px;width:300px}.submit{height:40px;width:150px;background:#ccc;border:2px solid #0A100D;-webkit-transition:.3s;transition:.3s;opacity:.6;font-size:20px;font-family:'Ubuntu Mono', monospace}.submit:hover{background:#851111;-webkit-transform:scale(1.1);transform:scale(1.1);border:1.5px solid #0A100D;opacity:0.8;cursor:pointer;color:#ccc}.submit:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.contact-title{margin-bottom:15%;font-size:55px}@media screen and (max-width: 430px){Form{width:300px}.formgroup{width:300px}.contact-title{margin-bottom:15%;font-size:45px}}



.text-align-left-center{text-align:left;width:400px;margin-left:auto;margin-right:auto;font-size:15px}@media screen and (max-width: 1370px){.text-align-left-center{width:300px}}@media screen and (max-width: 367px){.text-align-left-center{margin-left:1%;margin-right:1%}}

.blog-post{padding-left:35%;padding-right:35%;font-size:18px;font-family:'Fira Code';text-align:justify;line-height:1.1}.likes{font-size:18px;font-weight:bold}.like-icon{color:#354654;-webkit-transition:.2s;transition:.2s}.like-icon:hover{color:#0A100D;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.like-icon:active{color:#354654;-webkit-transform:scale(1);transform:scale(1)}.summary{position:fixed;text-align:left;color:#0A100D;top:70%}.summary .inactive_anchor:hover{color:#0A100D}.summary .active_anchor{color:#0A100D}.summary .inactive_anchor{color:#ccc}.title{margin-top:50px;margin-bottom:50px;font-family:'Ubuntu Mono', monospace;font-weight:bold;font-size:25px;opacity:.9;-webkit-transition:.5s;transition:.5s;color:#0A100D}.title:hover{-webkit-transform:scale(1.05);transform:scale(1.05);opacity:1;color:#354654;text-decoration:underline}.back-button{margin-top:20px;margin-left:auto;margin-right:auto;height:40px;width:150px;background:#ccc;border:2px solid #0A100D;-webkit-transition:.3s;transition:.3s;opacity:.6;font-size:18px;font-family:'Ubuntu Mono', monospace}.back-button:hover{background:#354654;-webkit-transform:scale(1.1);transform:scale(1.1);border:1.5px solid #0A100D;opacity:0.8;cursor:pointer;color:#ccc}#disqus_thread{margin-top:5%;width:600px;margin-left:auto;margin-right:auto}code{font-family:'Fira Code'}@media screen and (max-width: 1000px){.blog-post{padding-left:30%;padding-right:30%}}@media screen and (max-width: 800px){.blog-post{padding-left:20%;padding-right:20%}}@media screen and (max-width: 600px){.blog-post{text-align:left;line-height:1.2;padding-left:15%;padding-right:15%}#disqus_thread{width:300px}}

.custom-container{margin-left:auto;margin-right:auto;margin-top:0;margin-bottom:0;overflow-x:auto}.node circle{fill:#000000;stroke:#800000;stroke-width:1.5px}.node text{font-size:18px;font-family:"Helvetica Neue", Helvetica, Arial, sans-serif;background-color:black;fill:black;-webkit-transform:translate(-20px, -10px);transform:translate(-20px, -10px)}path.link{fill:none;stroke:#800000;stroke-width:1.5px}.back-button{margin-top:20px;margin-left:auto;margin-right:auto;height:40px;width:150px;background:#ccc;border:2px solid #0A100D;-webkit-transition:.3s;transition:.3s;opacity:.6;font-size:18px;font-family:'Ubuntu Mono', monospace}.back-button:hover{background:#354654;-webkit-transform:scale(1.1);transform:scale(1.1);border:1.5px solid #0A100D;opacity:0.8;cursor:pointer;color:#ccc}

body{margin:0;padding:0;background:#F7F2EF;font-family:'Ubuntu Mono', monospace;overflow-x:hidden}.App{text-align:center}::-webkit-scrollbar{width:12px}::-webkit-scrollbar-track{border:1px solid #354654;box-shadow:inset 0 0 2.5px 2px #ccc}::-webkit-scrollbar-thumb{background:#354654;border-radius:5px}::-webkit-scrollbar-thumb:hover{background:#0A100D}.trademarks{margin-top:50px}

